"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { useAuth } from "@/hooks/useAuth";
import { getPatients } from "@/utils/firestore/patients";
import { getAllReports, type ReportWithPatientInfo } from "@/utils/firestore/reports";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Users, FileText, Calendar, ArrowRight } from "lucide-react";
import { Timestamp } from "firebase/firestore";

export default function DashboardPage() {
  const { user: firebaseUser, loading: authLoading } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState({
    totalPatients: 0,
    totalReports: 0,
    loading: true,
  });

  useEffect(() => {
    if (!firebaseUser?.uid) {
      setStats({ totalPatients: 0, totalReports: 0, loading: false });
      return;
    }

    const fetchDashboardData = async () => {
      try {
        // Fetch patients count - get all patients
        let totalPatients = 0;
        let lastDoc = null;
        let hasMore = true;
        
        while (hasMore) {
          const patientsResult = await getPatients(firebaseUser.uid, 1000, lastDoc);
          totalPatients += patientsResult.patients.length;
          lastDoc = patientsResult.lastDoc;
          hasMore = patientsResult.hasMore;
        }

        // Fetch all reports to get accurate count
        let totalReports = 0;
        let lastReport = null;
        let hasMoreReports = true;
        
        while (hasMoreReports) {
          const reportsResult = await getAllReports(firebaseUser.uid, 100, lastReport || undefined);
          totalReports += reportsResult.reports.length;
          lastReport = reportsResult.lastReport;
          hasMoreReports = reportsResult.hasMore;
        }

        setStats({
          totalPatients,
          totalReports,
          loading: false,
        });
      } catch (error) {
        console.error("Failed to fetch dashboard data:", error);
        setStats({ totalPatients: 0, totalReports: 0, loading: false });
      }
    };

    fetchDashboardData();
  }, [firebaseUser?.uid]);

  if (authLoading || stats.loading) {
    return (
      <div className="space-y-6">
        <div>
          <Skeleton className="h-8 w-48 mb-2" />
          <Skeleton className="h-4 w-96" />
        </div>
        <div className="grid auto-rows-min gap-4 md:grid-cols-3">
          <Skeleton className="aspect-video rounded-xl" />
          <Skeleton className="aspect-video rounded-xl" />
          <Skeleton className="aspect-video rounded-xl" />
        </div>
        <Skeleton className="min-h-[400px] rounded-xl" />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold tracking-tight">Dashboard</h1>
        <p className="text-muted-foreground">
          Overview of your lab activities and statistics
        </p>
      </div>

      <div className="grid auto-rows-min gap-4 md:grid-cols-3">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Patients</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalPatients}</div>
            <p className="text-xs text-muted-foreground mt-1">
              Registered patients in your lab
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Reports</CardTitle>
            <FileText className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalReports}</div>
            <p className="text-xs text-muted-foreground mt-1">
              Pathology reports created
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Recent Activity</CardTitle>
            <Calendar className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{recentReports.length}</div>
            <p className="text-xs text-muted-foreground mt-1">
              Reports in the last period
            </p>
          </CardContent>
        </Card>
      </div>

      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle>Recent Reports</CardTitle>
              <CardDescription>
                Latest pathology reports created in your lab
              </CardDescription>
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={() => router.push("/dashboard/reports")}
            >
              View All
              <ArrowRight className="ml-2 h-4 w-4" />
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          {reportsLoading ? (
            <div className="space-y-4">
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
            </div>
          ) : recentReports.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-12 text-center">
              <FileText className="h-12 w-12 text-muted-foreground mb-4" />
              <h3 className="text-lg font-semibold mb-2">No reports yet</h3>
              <p className="text-muted-foreground mb-4">
                Start by creating your first pathology report.
              </p>
              <Button onClick={() => router.push("/dashboard/make-report")}>
                Create Report
              </Button>
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Date</TableHead>
                  <TableHead>Patient</TableHead>
                  <TableHead>Doctor</TableHead>
                  <TableHead>Tests</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {recentReports.map((report) => {
                  const reportDate =
                    report.date instanceof Timestamp
                      ? report.date.toDate()
                      : new Date(report.date);
                  const testCount = Object.keys(report.tests || {}).length;

                  return (
                    <TableRow
                      key={`${report.patientId}-${report.id}`}
                      className="cursor-pointer"
                      onClick={() =>
                        router.push(
                          `/dashboard/patients/${report.patientId}/reports`
                        )
                      }
                    >
                      <TableCell className="font-medium">
                        {reportDate.toLocaleDateString("en-US", {
                          year: "numeric",
                          month: "short",
                          day: "numeric",
                        })}
                      </TableCell>
                      <TableCell>{report.patientName}</TableCell>
                      <TableCell>{report.doctor}</TableCell>
                      <TableCell>{testCount} test(s)</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
